import { Component, OnInit } from '@angular/core';
import { Note } from './note.model';
import { NoteService } from './note.service';
import {Router} from "@angular/router";
import { ExportAsService, ExportAsConfig } from 'ngx-export-as';
import { FilterPipe } from 'ngx-filter-pipe';

@Component({
  selector: 'note',
  templateUrl: './note.component.html',
  styleUrls: ['./note.component.scss']
})
export class NoteComponent implements OnInit {
  
  noteList: Note[];
  userFilter: any = { content: '' };
  public textFilter:string = "";
  constructor(private router: Router, private filterPipe: FilterPipe, private noteService: NoteService, private exportAsService:ExportAsService) {
    
  }

  ngOnInit() {
    this.noteService.getNotes()
      .subscribe( data => {
        this.noteList = data as Note[];
      });
  }

  deleteNote(note: Note): void {
    this.noteService.deleteNote(note.id)
      .subscribe( data => {
        this.noteList = this.noteList.filter(u => u !== note);
      })
  };

  editNote(note: Note): void {
    localStorage.removeItem("editNoteId");
    localStorage.setItem("editNoteId", note.id.toString());
    this.router.navigate(['edit-note']);
  };

  addNote(): void {
    this.router.navigate(['add-note']);
  };

  exportAsConfig: ExportAsConfig = {
    type: 'csv', // the type you want to download
    elementId: 'myTableElementId', // the id of html/table element
  }

  export() {
    // download the file using old school javascript method
    this.exportAsService.save(this.exportAsConfig, 'My File Name');
    // get the data as base64 or json object for json type - this will be helpful in ionic or SSR
    // this.exportAsService.get(this.config).subscribe(content => {
    //   console.log(content);
    // });
  }

  applyNGXFilter(){
    // https://stackoverflow.com/questions/46289426/filtering-more-than-one-value-with-ngx-filter-pipe-angular
    this.userFilter = {
      $or : [ 
          { 'content' : this.textFilter} 
      ]
  }
  }

  
  
}
