import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {first} from "rxjs/operators";
import { Note } from './note.model';
import { NoteService } from './note.service';

@Component({
  selector: 'edit-note',
  templateUrl: './edit-note.component.html',
  styleUrls: ['./note.component.scss']
})
export class EditNoteComponent implements OnInit {

sprofile: Note;
editForm: FormGroup;
getDate: any = new Date().toLocaleString() ;
  constructor(private formBuilder: FormBuilder,private router: Router, private noteService: NoteService) { }

  ngOnInit() {
    let userId = localStorage.getItem("editNoteId");
    if(!userId) {
      alert("Invalid action.")
      this.router.navigate(['note']);
      return;
    }
    this.editForm = this.formBuilder.group({
      id: [],
      title: ['', Validators.required],
      content: ['', Validators.required],
      noteTimeStamp: [this.getDate, Validators.required]
    });
    this.noteService.getNoteById(+userId)
      .subscribe( data => {
        this.editForm.setValue(data);
      });
  }

  onSubmit() {
    this.noteService.updateNote(this.editForm.value)
      .pipe(first())
      .subscribe(
        data => {
          this.router.navigate(['note']);
        },
        error => {
          alert(error);
        });
  }

}