import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AutomateCustomMaterialModule } from './shared/custom.material';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NoteComponent } from './new-note/note.component';
import { AddNoteComponent } from './new-note/add-note.component';
import { EditNoteComponent } from './new-note/edit-note.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { InMemoryWebApiModule } from 'angular-in-memory-web-api';  
import { DataService } from './data.service';
import { FilterPipeModule } from 'ngx-filter-pipe';
import { ExportAsModule } from 'ngx-export-as';
import { StoreModule } from '@ngrx/store';
import { reducers, metaReducers } from './reducers';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';
import { environment } from '../environments/environment';
import { NoteOverviewComponent } from './note-overview/note-overview.component';

@NgModule({
  declarations: [
    AppComponent,
    NoteComponent,
    AddNoteComponent,
    EditNoteComponent,
    NoteOverviewComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    AutomateCustomMaterialModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    FilterPipeModule,
    ExportAsModule,
    InMemoryWebApiModule.forRoot(DataService),
    AppRoutingModule,
    StoreModule.forRoot(reducers, {
      metaReducers, 
      runtimeChecks: {
        strictStateImmutability: true,
        strictActionImmutability: true,
      }
    }),
    !environment.production ? StoreDevtoolsModule.instrument() : []
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
