import { Action, createAction, props } from '@ngrx/store';
import { Note } from '../new-note/note.model';

export enum AuditDataActionTypes {
  ADD_NOTE = '[Note] Add Note',
  REMOVE_NOTE = '[Note] Remove Note'
}

export class ADD_NOTE implements Action {
  readonly type = AuditDataActionTypes.ADD_NOTE;

  constructor(public _note : {notesLength: Number, lastupdateDate:Date}){}
}

export class REMOVE_NOTE implements Action {
  readonly type = AuditDataActionTypes.REMOVE_NOTE;

  constructor(public _note : {notesLength: Number, lastupdateDate:Date}){}
}

export type AuditDataActions = ADD_NOTE | REMOVE_NOTE;