import { Action, createReducer, on } from '@ngrx/store';
import { AuditDataActionTypes, ADD_NOTE, REMOVE_NOTE } from '../actions/audit-data.actions'
import { Note } from '../new-note/note.model';
import { NoteService } from '../new-note/note.service';

export const auditNoteFeatureKey = 'auditNote';

export interface NoteAppState {
  lastupdateDate:Date;
}

export const initialState: NoteAppState = {
  lastupdateDate: new Date()
};

export function reducer(state = initialState , action: any):NoteAppState {
  switch (action.type){
    case AuditDataActionTypes.ADD_NOTE:
    return Object.assign({}, state, {lastupdateDate : action._note.noteTimeStamp
    })
    case AuditDataActionTypes.REMOVE_NOTE:
    return Object.assign({}, state, {
      lastupdateDate : action._note.noteTimeStamp
    })
    
    default: return state;
  }
}
