import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { NoteComponent } from './new-note/note.component';
import { AddNoteComponent } from './new-note/add-note.component';
import { EditNoteComponent } from './new-note/edit-note.component';
import { AppComponent } from './app.component';

const routes: Routes = [{ path: 'note', component: NoteComponent },
                        { path: 'add-note', component: AddNoteComponent },
                        { path: 'edit-note', component: EditNoteComponent },
                        { path: 'app-root', component: AppComponent },
                        { path: '**', redirectTo: '/note', pathMatch: 'full' }];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
