import { Component, OnInit } from '@angular/core';
import { NoteAppState } from '../reducers/audit-note.reducer';
import { Store } from '@ngrx/store';
@Component({
  selector: 'note-overview',
  templateUrl: './note-overview.component.html',
  styleUrls: ['./note-overview.component.css']
})
export class NoteOverviewComponent implements OnInit {
  lastupdateDate: Date;
  storeData:any;
  getDate: any = new Date().toLocaleString() ;
  constructor(private store:Store<NoteAppState>) { 
   store.select(state => state.lastupdateDate)
    .subscribe(lastupdateDate => this.lastupdateDate = lastupdateDate);
  }

  ngOnInit() {
    this.store.select(state => state.lastupdateDate).subscribe(data => {
      console.log(data);
      this.storeData = this.store;
      this.getDate = this.storeData.actionsObserver._value._note.noteTimeStamp;
    });
  }

}
