import { Component, OnInit } from '@angular/core';
import { NoteService } from './note.service';
import {Router} from "@angular/router";
import { NoteAppState } from '../reducers/audit-note.reducer';
import { Store, select } from '@ngrx/store';
// import { ADD_NOTE } from '../actions/audit-data.actions';


import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import { ADD_NOTE } from '../actions/audit-data.actions';

@Component({
    selector: 'add-note',
    templateUrl: './add-note.component.html',
    styleUrls: ['./note.component.scss']
  })
  
export class AddNoteComponent implements OnInit {
    constructor(private formBuilder: FormBuilder,private router: Router, 
                private noteService: NoteService, private _store: Store<NoteAppState>) { }

  addForm: FormGroup;
  getDate: any = new Date().toLocaleString() ;
  ngOnInit() {

    this.addForm = this.formBuilder.group({
      id: [],
      title: ['', Validators.required],
      content: ['', Validators.required],
      noteTimeStamp: [this.getDate, Validators.required]
    });

  }

  onSubmit() {
    this.noteService.createNote(this.addForm.value)
      .subscribe( data => {
        let val:any = Object.assign({}, data);
        this._store.dispatch(new ADD_NOTE(val));
        this.router.navigate(['note']);
      });
  }
    
}